import React from "react";
import Giph from "../component/Giph";
import { Link, Outlet } from "react-router-dom";

const Search = (props) => {
  let renderGifs = () => {
    if (props.gifs !== []) {
      return props.gifs.map((gif) => {
        return <Giph gif={gif} addToFavorites={props.addToFavorites} />;
      });
    }
  };

  return (
    <div>
      <div className="move">
        <Link className="favorites" to="/favorites">
          Favorite
        </Link>
      </div>
      <div className="form">
        <form onSubmit={props.searchGif}>
          <input
            className="input"
            type="text"
            placeholder="Search Giphy"
            value={props.search}
            onChange={props.onChangeText}
          />
          <input className="button" type="submit" />
        </form>
      </div>
      {renderGifs()}
    </div>
  );
};

export default Search;
