import React, { useState } from "react";

const Favorites = (props) => {
  const [mainFav, setMainFav] = useState();
  let renderMain = () => {
    if (mainFav) {
      return (
        <img
          src={mainFav.images.downsized.url}
          style={{ height: 500, width: 800 }}
          alt=""
        />
      );
    }
  };
  let renderFavorites = () => {
    return props.favorites.map((favorite) => {
      return (
        <div onClick={() => setMainFav(favorite)}>
          <h4>{favorite.title}</h4>
          <img src={favorite.images.downsized.url} alt="" />
        </div>
      );
    });
  };

  return (
    <div>
      {renderMain()}
      {renderFavorites()}
    </div>
  );
};

export default Favorites;
