import React from "react";
import Giph from "../component/Giph";
import { Link } from "react-router-dom";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import Search from "./Search";
import Favorites from "./Favorites";
import "../index.css";

let apiKEY = "sJM23fX2iA60b4OrdSnJXLs88JUeNvwp";

class Main extends React.Component {
  constructor() {
    super();
    this.state = {
      search: "",
      gifs: [],
      favorites: [],
    };
  }

  addToFavorites = (gif) => {
    this.setState({
      favorites: [...this.state.favorites, gif],
    });
  };
  onChangeText = (e) => {
    this.setState({
      search: e.target.value,
    });
  };

  searchGif = (e) => {
    e.preventDefault();

    fetch(
      `https://api.giphy.com/v1/gifs/search?api_key=${apiKEY}&q=${this.state.search}`
    )
      .then((response) => response.json())
      .then((data) =>
        this.setState({
          gifs: data.data,
        })
      )
      .catch((err) => console.log(err));
  };

  renderGifs = () => {
    if (this.state.gifs !== []) {
      return this.state.gifs.map((gif) => {
        return <Giph gif={gif} addToFavorites={this.addToFavorites} />;
      });
    }
  };

  render() {
    return (
      <BrowserRouter>
        <Routes>
          <Route
            className="route"
            path="/"
            element={
              <Search
                onChangeText={this.onChangeText}
                addToFavorites={this.addToFavorites}
                gifs={this.state.gifs}
                searchGif={this.searchGif}
                search={this.state.search}
              />
            }
          />
          <Route
            path="favorites"
            element={<Favorites favorites={this.state.favorites} />}
          />
        </Routes>
      </BrowserRouter>
    );
  }
}

export default Main;
